
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Oswald:wght@500&family=Ubuntu:wght@300&display=swap');

:root{
    --primary-app-color:#101024;
    --secondary-app-color:#1a1938;
    --standout-color:#fffb00
}

*{
    padding: 0;
    margin: 0;
}
.herosection{
    background: linear-gradient(90deg,var(--primary-app-color),var(--secondary-app-color));
    clip-path: polygon(0 0, 100% 0, 100% 84%, 53% 100%, 0 85%);
}
.nav-items{
    cursor: pointer;
    color: #fff;
    font-size: 1.3rem;    
}
.brand-name{
    font-family: "Oswald";
    color: #fff;
    font-size: 2rem;
}
.px-10{
    padding-inline: 90px;
}
.herosection-2{
    padding-top: 60px;

}
.I-m-text{
    padding-top: 52px;
    text-align: center;
}
.Im-text{
    font-family:"Ubuntu";
    color: #fff;
    font-weight: 500;
    font-size: 1.4rem;
}
.name{
color: var(--standout-color);
font-size: 28px;

}
.designation{
 font-family: sans-serif;
 color: #fff;
font-size: 3rem;
}
.description{
    font-family: "Open-sans";
    color: #fff;
    font-size: 1.4rem;
}
.hire-btn {
    font-size: 1.3rem;
    font-family: "Ubuntu";
    background: transparent;
    background-color: #090b24;
    color: white;
    border-radius: 25px;
    margin-inline: 12px;
    padding: 5px 40px;
    border: 2px solid white;
    transition:all 0.3s ease-in-out;
    animation:Anim 1s ease-in-out infinite ;
}
.hire-btn:hover{
    transform: translateY(-5px);
    background-color: var(--standout-color);
    color: #1c1c1c;
}
.get-btn {
   font-size: 1.3rem;
   font-family: "Ubuntu";
   color: #fff;
   background:orangered;
   border-radius: 25px;
   margin-inline: 12px;
   padding: 5px 40px;
   border: 2px solid white;
   transition:all 0.3s ease-in-out;

}

.get-btn:hover {
  transform: translateY(-5px);
  background-color: cornsilk;
  color: #111;
}
@keyframes Anim {
    0%{
        transform: scale(1);
    }
    50%{
            transform: scale(1.1);
    }
    100%{
            transform: scale(1);
    }
}
 .custom-img{
  
    width: 300px;
    object-fit: contain;
    border-radius:50% 50%;
    display: flex;
    justify-content: center;
    padding-bottom: 20px;
    border: 1px solid white;
    
}
.img{
    width: 300px;
    border-radius: 50%;
    
}
.img-container{
    justify-content: center;
    padding-inline-start: 10%;
}