.contact-me-part{
    color:#fff;

}
.relative-bg{
    clip-path: polygon(0 5%, 100% 17%, 100% 83%, 0 94%);
    background: linear-gradient(-90deg,var(--primary-app-color),var(--secondary-app-color));
    height: 100%;
    width :100%;
    position: absolute;
    z-index: -4;
}
.contact-me-card{
    background: #1c1c1c;
    width: 80%;
    box-shadow: 10px 10px 63px 4px rgb(243, 240, 240);
    height: 500px;
    margin-inline:auto ;
}
.get-in-touch{
  color: #fff; 
  font-family: "Ubuntu";
  font-weight: 700;
  font-size: 2rem; 
}
.input-group{
border: none;
border-radius: 10px;

}
.card-contact-right{
    background: rgb(0, 7, 61);
    border-radius: 20px;
    width: 90%;
    padding: 24px ;
    box-shadow: 10px 10px 63px 4px rgb(243, 240, 240);
}
.contact-me-inner-part{
    padding-block: 110px;
}
.labels{
    font-family: "Ubuntu";
    font-size: 1.2rem;
 
}